    <span class="hljs-tag">&lt;<span class="hljs-name">vc-viewer</span> <span class="hljs-attr">@ready</span>=<span class="hljs-string">"ready"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">vc-entity</span> <span class="hljs-attr">ref</span>=<span class="hljs-string">"entity1"</span> <span class="hljs-attr">:position</span>=<span class="hljs-string">"position1"</span> <span class="hljs-attr">:description</span>=<span class="hljs-string">"description"</span> <span class="hljs-attr">:box.sync</span>=<span class="hljs-string">"box1"</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">vc-graphics-box</span> <span class="hljs-attr">:dimensions</span>=<span class="hljs-string">"dimensions1"</span> <span class="hljs-attr">:material</span>=<span class="hljs-string">"material1"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">vc-graphics-box</span>&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">vc-entity</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">vc-entity</span> <span class="hljs-attr">ref</span>=<span class="hljs-string">"entity2"</span> <span class="hljs-attr">:position</span>=<span class="hljs-string">"position2"</span> <span class="hljs-attr">:description</span>=<span class="hljs-string">"description"</span> <span class="hljs-attr">:box.sync</span>=<span class="hljs-string">"box2"</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">vc-graphics-box</span>
