        &gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">vc-graphics-box</span>&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">vc-entity</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">vc-entity</span> <span class="hljs-attr">ref</span>=<span class="hljs-string">"entity3"</span> <span class="hljs-attr">:position</span>=<span class="hljs-string">"position3"</span> <span class="hljs-attr">:description</span>=<span class="hljs-string">"description"</span> <span class="hljs-attr">:box.sync</span>=<span class="hljs-string">"box3"</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">vc-graphics-box</span> <span class="hljs-attr">:dimensions</span>=<span class="hljs-string">"dimensions3"</span> <span class="hljs-attr">:outlineColor</span>=<span class="hljs-string">"outlineColor3"</span> <span class="hljs-attr">:fill</span>=<span class="hljs-string">"false"</span> <span class="hljs-attr">:outline</span>=<span class="hljs-string">"true"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">vc-graphics-box</span>&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">vc-entity</span>&gt;</span>
